'use client'

import { useState, useEffect, useRef } from 'react'

interface Project {
  id: number
  title: string
  description: string
  image: string
  technologies: string[]
  github: string
  demo: string
  category: string
  gradient: string
}

const projects: Project[] = []

export default function Projects() {
  const [hoveredProject, setHoveredProject] = useState<number | null>(null)
  const [visibleProjects, setVisibleProjects] = useState<Set<number>>(new Set())
  const projectRefs = useRef<(HTMLDivElement | null)[]>([])

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const index = projectRefs.current.indexOf(entry.target as HTMLDivElement)
            if (index !== -1) {
              setTimeout(() => {
                setVisibleProjects(prev => new Set([...prev, index]))
              }, index * 200)
            }
          }
        })
      },
      { threshold: 0.1 }
    )

    projectRefs.current.forEach(ref => {
      if (ref) observer.observe(ref)
    })

    return () => observer.disconnect()
  }, [])

  return (
    <section className="py-24 px-4 section-bg-mesh relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute inset-0 pattern-dots opacity-10"></div>
      <div className="absolute top-20 right-20 w-40 h-40 bg-gradient-to-br from-red-500/10 to-red-600/10 rounded-full blur-3xl"></div>
      <div className="absolute bottom-20 left-20 w-60 h-60 bg-gradient-to-br from-blue-500/10 to-purple-500/10 rounded-full blur-3xl"></div>

      <div className="max-w-7xl mx-auto relative z-10">
        <div className="text-center mb-20 slide-in-up">
          <div className="inline-block mb-6">
            <span className="badge-modern text-sm font-medium px-6 py-2">Featured Work</span>
          </div>
          <h2 className="text-4xl md:text-6xl font-bold bg-gradient-to-r from-gray-100 via-white to-gray-200 bg-clip-text text-transparent mb-6 animate-gradient-x">
            Featured Projects
          </h2>
          <p className="text-xl md:text-2xl text-gray-300 max-w-3xl mx-auto leading-relaxed font-light">
            A collection of projects I've worked on that showcase my development skills and passion for creating{' '}
            <span className="text-gradient-red font-semibold">exceptional digital experiences</span>
          </p>
          <div className="divider-modern mt-8"></div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {projects.length === 0 ? (
            <div className="col-span-full text-center py-24 slide-in-up">
              <div className="relative mb-8">
                <div className="text-8xl mb-6 animate-bounce">üöÄ</div>
                <div className="absolute inset-0 bg-gradient-to-r from-red-500/20 to-blue-500/20 rounded-full blur-2xl scale-150"></div>
              </div>
              <h3 className="text-3xl font-bold text-gradient mb-4">Coming Soon...</h3>
              <p className="text-gray-400 text-lg max-w-md mx-auto leading-relaxed">
                My projects will appear here soon. I'm currently working on some exciting new projects!
              </p>
              <div className="mt-8">
                <div className="inline-flex items-center gap-3 glass-strong px-6 py-3 rounded-2xl">
                  <div className="spinner-modern"></div>
                  <span className="text-gray-300">Building amazing things...</span>
                </div>
              </div>
            </div>
          ) : (
            projects.map((project, index) => (
            <div
              key={project.id}
              ref={(el) => { projectRefs.current[index] = el }}
              className={`group relative glass-card rounded-3xl shadow-2xl hover:shadow-3xl transition-all duration-700 overflow-hidden border border-white/10 card-hover ${
                visibleProjects.has(index) ? 'opacity-100 translate-y-0 scale-100' : 'opacity-0 translate-y-12 scale-95'
              }`}
              onMouseEnter={() => setHoveredProject(project.id)}
              onMouseLeave={() => setHoveredProject(null)}
            >
              {/* Project Image */}
              <div className="relative h-56 overflow-hidden">
                <div className={`absolute inset-0 bg-gradient-to-br ${project.gradient} opacity-20 group-hover:opacity-30 transition-opacity duration-500`}></div>
                <img
                  src={project.image}
                  alt={project.title}
                  className="w-full h-full object-cover transition-all duration-700 group-hover:scale-110 group-hover:rotate-1"
                  onError={(e) => {
                    e.currentTarget.src = 'data:image/svg+xml;base64,'
                  }}
                />

                {/* Enhanced Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-60 group-hover:opacity-80 transition-opacity duration-500"></div>

                {/* Category Badge */}
                <div className="absolute top-6 right-6">
                  <span className="badge-modern text-xs font-medium px-4 py-2 shadow-lg">
                    {project.category}
                  </span>
                </div>

                {/* Enhanced Hover Actions */}
                <div className={`absolute inset-0 flex items-center justify-center space-x-6 rtl:space-x-reverse transition-all duration-500 ${
                  hoveredProject === project.id ? 'opacity-100 translate-y-0 scale-100' : 'opacity-0 translate-y-4 scale-95'
                }`}>
                  <a
                    href={project.github}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="group/btn bg-white/95 dark:bg-gray-800/95 backdrop-blur-xl p-4 rounded-2xl hover:bg-white dark:hover:bg-gray-700 transition-all duration-300 shadow-xl hover:shadow-2xl transform hover:scale-110 tooltip"
                    data-tooltip="View Source Code"
                  >
                    <svg className="w-6 h-6 text-gray-700 dark:text-gray-300 group-hover/btn:text-gray-900 dark:group-hover/btn:text-white transition-colors duration-300" fill="currentColor" viewBox="0 0 24 24">
                      <path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/>
                    </svg>
                  </a>
                  <a
                    href={project.demo}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="group/btn bg-gradient-to-r from-red-600 to-red-700 text-white p-4 rounded-2xl hover:from-red-500 hover:to-red-600 transition-all duration-300 shadow-xl hover:shadow-red-500/50 transform hover:scale-110 btn-hover-glow tooltip"
                    data-tooltip="View Live Demo"
                  >
                    <svg className="w-6 h-6 transition-transform duration-300 group-hover/btn:rotate-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14"/>
                    </svg>
                  </a>
                </div>
              </div>

              {/* Project Content */}
              <div className="p-8">
                <h3 className="text-2xl font-bold text-gradient mb-3 group-hover:text-white transition-colors duration-300">
                  {project.title}
                </h3>
                <p className="text-gray-300 mb-6 line-clamp-3 leading-relaxed">
                  {project.description}
                </p>

                {/* Technologies */}
                <div className="flex flex-wrap gap-3 mb-6">
                  {project.technologies.map((tech, techIndex) => (
                    <span
                      key={techIndex}
                      className="bg-gray-700/80 text-gray-300 text-sm px-3 py-1 rounded-full hover:bg-gray-600 hover:text-white transition-all duration-300 glass border border-white/10"
                    >
                      {tech}
                    </span>
                  ))}
                </div>

                {/* Action Buttons */}
                <div className="flex space-x-4 rtl:space-x-reverse">
                  <a
                    href={project.github}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex-1 bg-gray-800/80 dark:bg-gray-700/80 text-white text-center py-3 px-4 rounded-2xl hover:bg-gray-700 dark:hover:bg-gray-600 transition-all duration-300 text-sm font-semibold glass border border-white/10 hover:border-white/20"
                  >
                    <span className="flex items-center justify-center gap-2">
                      <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/>
                      </svg>
                      Code
                    </span>
                  </a>
                  <a
                    href={project.demo}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="flex-1 bg-gradient-to-r from-red-600 to-red-700 text-white text-center py-3 px-4 rounded-2xl hover:from-red-500 hover:to-red-600 transition-all duration-300 text-sm font-semibold shadow-lg hover:shadow-red-500/30 transform hover:-translate-y-0.5"
                  >
                    <span className="flex items-center justify-center gap-2">
                      Demo
                      <svg className="w-4 h-4 transition-transform duration-300 group-hover:translate-x-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7"/>
                      </svg>
                    </span>
                  </a>
                </div>
              </div>
            </div>
            ))
          )}
        </div>

        <div className="text-center mt-16 slide-in-up" style={{animationDelay: '0.5s'}}>
          <a
            href="/projects"
            className="inline-flex items-center gap-3 bg-gradient-to-r from-red-600 via-red-500 to-red-600 text-white px-10 py-4 rounded-2xl font-bold text-lg hover:from-red-500 hover:via-red-400 hover:to-red-500 transition-all duration-500 shadow-2xl hover:shadow-red-500/30 transform hover:-translate-y-1 btn-hover-glow"
          >
            <span>View All Projects</span>
            <svg className="w-6 h-6 transition-transform duration-300 group-hover:translate-x-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7"/>
            </svg>
          </a>
        </div>
      </div>
    </section>
  )
}
